.intro {

    display: flex;

    align-items: center;
    width: 100%;
    height: 790px;
    -webkit-background-size: cover;
    background-size: cover;

    background: url('../css/image/intro/bg-intro.jpg') center no-repeat;

    &-inner {

        display: flex;
        align-items: center;

        &_item {

            display: flex;
            align-items: center;

            width: 50%;

            &_content {
                &_text {
                    &_title {

                        font-family: $ff-as;
                        font-weight: $fw500;
                        font-size: $px-90;
                        color: $great; }

                    &_subtitle {

                        font-family: $ff-as;
                        font-weight: $fw500;
                        font-size: $px-22;
                        color: $great; } }

                &_button {

                    margin-top: $px-50;

                    &_more, &_hire {

                        font-family: $ff-as;
                        font-weight: $fw500;
                        font-size: $px-16;
                        color: $great;

                        border: 1px solid $great;

                        padding: $px-20 $px-30;

                        transition: all .2s linear;

                        z-index: 250;
                        position: sticky;


                        &:hover {

                            background: $white;
                            color: $black;

                            border: 1px solid $white;

                            box-shadow: 0 10px 6px -6px rgba(0, 0, 0, .3); } }

                    &_hire {
                        padding: $px-20 $px-45;
                        margin-left: $px-15; } } } }

        &_face {
            display: flex;
            justify-content: center;
            position: relative;

            img {
                position: absolute;
                bottom: -397px;
                left: -30px; } } } }

.modal {

    position: fixed;
    z-index: 2000;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .8);
    display: none;



    &.show {
        display: block; }




    &-icon {
        position: absolute;
        width: 400px;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        padding: 40px;
        background: $white;
        color: $black;
        text-align: center;

        opacity: 0;
        transition: all .2s linear; }



    &-close {
        position: absolute;
        top: -15px;
        right: 15px;
        width: 25px;
        height: 23px;
        cursor: pointer;

        &::before, &::after {
            content: "";
            background: $black;
            position: absolute;
            height: 2px;
            width: 31px;
            top: 30px;
            left: -4px; }

        &::before {
            transform: rotate(45deg); }

        &::after {
            transform: rotate(-45deg); } } }

@keyframes fade {
    from {
         opacity: 0; } }
to {
     opacity: 1; }





